<template>
	<div  >
		<div class="loginbao">
			<div class="loginlogo">
				<img src="../img/login.png"/>
			</div>
			<input type="text" name="" id="userName" value="" placeholder=""/>
			<input type="password" name="" id="password" value="" placeholder=""/>
			<div @click="denglu()" id="button" ></div>
			<p></p>
			<p><span></span></p>
			<div id="toast"></div>
		</div>
	</div>
</template>

<script>
	import "./../scss/login.scss"
	import MyAjax from "./../md/MyAjax.js";
	import Toast from "./../md/Toast.js";

	export default {
		data () {
			return {
 			}
		},
		methods: {
			 denglu(){
			 	var userID = $("#userName").val();
				var password = $("#password").val();
			 
				if(userID == "" || password == ""){
					Toast.makeText("",2000);
				}else{
 					$("#button").attr("disabled","disabled");//
					$("#button").html("...")
					var that = this;
					var url ="http://datainfo.duapp.com/shopdata/userinfo.php?status=login&userID="+userID+"&password="+password;
					MyAjax.vueJson(url,function(data){
						$("#button").removeAttr("disabled")//
						$("#button").html("")
						console.log(data);
						
						if(data == "0"){
							//
							Toast.makeText("",1000);
		 				}else if(data == "2"){
							//
							Toast.makeText("",1000);
		 				}else{				
							//		
							
							localStorage.setItem("isLogin","1");
							localStorage.setItem("userID",userID);
   							that.$router.push({path: '/home'});
						}
					},function(err){
						$("#button").removeAttr("disabled")//
						$("#button").val("")
						console.log(err);
					});
 				}
			 }
		}
	}
	
</script>

<style scoped>
 
</style>


